<template>
    <header
        class="fixed w-full top-0 left-0 right-0"
        :style="{ backgroundColor: activeColor }"
        :class="logoInvert && 'border-b'"
    >
        <nav class="flex items-center justify-between px-4 py-6 md:px-8 xl:px-0 ">
            <div class="flex  items-center">
                <img
                    src="../assets/images/logo.png"
                    class="w-24 mr-8"
                    :class="logoInvert && 'hidden'"
                    alt=""
                />
                <img
                    src="../assets/images/logo-white.png"
                    :class="!logoInvert && 'hidden'"
                    class="w-24 mr-8"
                    alt=""
                />
                <ul
                    class="xl:flex space-x-8 text-sm font-semibold hidden"
                    :class="logoInvert && 'text-white'"
                >
                    <li class="flex items-center space-x-2">
                        <span>Product</span>
                        <div class="text-xs">
                            <i class="fa fa-chevron-down" aria-hidden="true"></i>
                        </div>
                    </li>

                    <li class="c-nav--primary__listitem">
                        <a
                            href="https://slack.com/enterprise"
                            data-clog-click=""
                            data-clog-ui-element="link_enterprise"
                            data-clog-ui-component="inc_header_nav"
                            >Enterprise</a
                        >
                    </li>
                    <li class="c-nav--primary__listitem">
                        <a
                            href="https://slack.com/resources"
                            data-clog-click=""
                            data-clog-ui-element="link_resources_library"
                            data-clog-ui-component="inc_header_nav"
                            >Resources</a
                        >
                    </li>
                    <li class="c-nav--primary__listitem">
                        <a
                            href="https://slack.com/pricing"
                            data-clog-click=""
                            data-clog-ui-element="link_pricing"
                            data-clog-ui-component="inc_header_nav"
                            >Pricing</a
                        >
                    </li>
                </ul>
            </div>
            <div class="flex mr-4 space-x-8 items-center" :class="logoInvert && 'text-white'">
                <div class="relative  rounded-md">
                    <input
                        type="search"
                        placeholder="Search"
                        class="hidden md:block rounded-md px-4 py-2 outline-none border border-purpleLight text-sm placeholder-gray-600"
                    />
                    <div class="md:absolute" id="searchBtn">
                        <i class="fa fa-search" aria-hidden="true"></i>
                    </div>
                </div>
                <span class="text-sm font-semibold whitespace-nowrap hidden xl:block">Sign in</span>
                <div class="xl:flex space-x-4 rounded hidden">
                    <button class="w-full">talk to sales</button>
                    <button
                        class="w-full"
                        :style="{
                            color: yPos > 1720 ? 'white' : '#611f69',
                            borderColor: yPos > 1720 ? 'white' : '#611f69',
                        }"
                    >
                        try for free
                    </button>
                </div>
                <div class="xl:hidden">
                    <i class="fa fa-bars" aria-hidden="true"></i>
                </div>
            </div>
        </nav>
    </header>
</template>

<script>
export default {
    data() {
        return {
            colors: ["#f6efe8", "white", "#4a154b"],
            yPos: "",
            windowWidth: "",
            activeColor: "#f6efe8",
            logoInvert: false,
        }
    },
    mounted() {
        window.addEventListener("scroll", this.onScroll)
        window.addEventListener("resize", this.onScroll)
    },
    beforeUnmount() {
        window.removeEventListener("scroll", this.onScroll)
        window.removeEventListener("resize", this.onScroll)
    },
    methods: {
        onScroll() {
            this.yPos = window.top.scrollY
            this.windowWidth = window.innerWidth
            if (this.windowWidth < 768) {
                if (this.yPos < 1502) {
                    this.activeColor = this.colors[0]
                    this.logoInvert = false
                } else if (this.yPos > 1502 && this.yPos < 2444) {
                    this.activeColor = "white"
                    this.logoInvert = false
                } else {
                    this.activeColor = this.colors[2]
                    this.logoInvert = true
                }
            } else if (this.windowWidth > 768 && this.windowWidth < 1200) {
                if (this.yPos < 1456) {
                    this.activeColor = this.colors[0]
                    this.logoInvert = false
                } else if (this.yPos > 1456 && this.yPos < 2250) {
                    this.activeColor = this.colors[1]
                    this.logoInvert = false
                } else {
                    this.activeColor = this.colors[2]
                    this.logoInvert = true
                }
            } else if (this.windowWidth > 1230) {
                if (this.yPos < 916) {
                    this.activeColor = this.colors[0]
                    this.logoInvert = false
                } else if (this.yPos > 916 && this.yPos < 1720) {
                    this.activeColor = this.colors[1]
                    this.logoInvert = false
                } else {
                    this.activeColor = this.colors[2]
                    this.logoInvert = true
                }
            }
        },
    },
}
</script>

